*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Encode Sans Expanded',sans-serif;
}

::-webkit-scrollbar {
  width: 4px !important;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 1px white; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #01bf71; 
    border-radius: 10px;
  }

  ::-webkit-scrollbar-track-piece{
    background: white;   
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #008e53; 
  }
